import {
  CardMedia,
  Card,
  CardActions,
  CardContent,
  Button,
  Typography,
  Tooltip,
  Box,
  Skeleton,
} from "@mui/material";
import { LazyLoadImage } from "react-lazy-load-image-component";
import "react-lazy-load-image-component/src/effects/blur.css";
import ArrowForwardIosIcon from "@mui/icons-material/ArrowForwardIos";
import PropTypes from "prop-types";

const MediaCard = ({ product, loading }) => {
  return (
    <Card
      sx={{
        display: "flex", // ใช้การแสดงผลแบบ flex เพื่อจัดการโครงสร้างภายใน
        flexDirection: "column", // จัดเรียงเนื้อหาในแนวตั้ง
        margin: "0 5px",
        boxShadow: 0,
        backgroundColor: "#f0f2f5",
        border: "2px solid #e0e0e0",
        borderRadius: "8px",
        overflow: "hidden",
        minHeight: "600px", // ปรับความสูงขั้นต่ำให้มากขึ้นเพื่อรองรับรูปภาพ 350px
        maxHeight: "600px", // ปรับความสูงสูงสุดให้สอดคล้องกับความสูงขั้นต่ำ
      }}
    >
      {loading ? (
        <Skeleton
          variant="rectangular"
          width="100%"
          height="350px" // ปรับความสูงของ Skeleton เพื่อสอดคล้องกับ LazyLoadImage
          animation="wave"
        />
      ) : (
        <LazyLoadImage
          alt={product.title}
          effect="blur"
          src={product.image}
          width="100%"
          style={{
            height: "350px",
            objectFit: "cover",
            borderRadius: "8px 8px 0 0",
          }}
        />
      )}

      <CardContent sx={{ flexGrow: 1, overflow: "auto" }}>
        {loading ? (
          <>
            <Skeleton width="60%" height="20px" />
            <Skeleton width="80%" height="20px" style={{ marginTop: "16px" }} />
            <Skeleton width="90%" height="20px" style={{ marginTop: "16px" }} />
            <Skeleton
              width="100%"
              height="20px"
              style={{ marginTop: "16px" }}
            />
          </>
        ) : (
          <>
            <Tooltip
              title={
                <Typography sx={{ fontSize: "1.2rem" }}>
                  {product.title}
                </Typography>
              }
              placement="top"
              arrow
              sx={{
                textDecoration: "underline",
                cursor: "pointer",
              }}
            >
              <Box
                sx={{
                  maxWidth: "100%",
                  display: "-webkit-box",
                  WebkitLineClamp: 2,
                  WebkitBoxOrient: "vertical",
                  overflow: "hidden",
                  textOverflow: "ellipsis",
                  cursor: "pointer",
                }}
              >
                <Typography gutterBottom variant="h5" component="div">
                  {product.title}
                </Typography>
              </Box>
            </Tooltip>

            <Tooltip
              title={
                <Typography sx={{ fontSize: "1.2rem" }}>
                  {product.description}
                </Typography>
              }
              placement="top"
              arrow
              sx={{
                textDecoration: "underline",
                cursor: "pointer",
              }}
            >
              <Box
                sx={{
                  maxWidth: "100%",
                  display: "-webkit-box",
                  WebkitLineClamp: 3,
                  WebkitBoxOrient: "vertical",
                  overflow: "hidden",
                  textOverflow: "ellipsis",
                  cursor: "pointer",
                }}
              >
                <Typography
                  color="text.secondary"
                  sx={{
                    fontSize: "1.1em",
                  }}
                >
                  {product.description}
                </Typography>
              </Box>
            </Tooltip>
          </>
        )}
      </CardContent>
      <CardActions sx={{ justifyContent: "flex-end" }}>
        {loading ? (
          <Skeleton width="20%" height="40px" />
        ) : (
          <Button
            size="small"
            href={product.link}
            endIcon={<ArrowForwardIosIcon />}
            sx={{
              backgroundColor: "#1976d2 !important",
              color: "#fff !important",
              "&:hover": {
                backgroundColor: "#1565c0 !important",
              },
              textDecoration: "unset !important",
            }}
          >
            See More
          </Button>
        )}
      </CardActions>
    </Card>
  );
};

MediaCard.propTypes = {
  product: PropTypes.shape({
    id: PropTypes.string.isRequired,
    title: PropTypes.string.isRequired,
    description: PropTypes.string.isRequired,
    image: PropTypes.string.isRequired,
    link: PropTypes.string.isRequired,
    status: PropTypes.string.isRequired,
  }),
  loading: PropTypes.bool,
};

MediaCard.defaultProps = {
  product: {
    id: "",
    title: "No title",
    description: "No description available",
    image:
      "http://localhost:10004/wp-content/uploads/2023/12/da75fe12-7ecc-463d-bf5b-62e0b96ed519.webp",
    link: "#",
    status: "draft",
  },
  loading: false,
};

export default MediaCard;
