import { useEffect, useState } from "react";
import { CarouselProduct } from "@/component";
import { useFetchProducts } from "@/store";
import { getProductCarousel } from "@/service";

const testCarouselId = 196;

const CarouselCon = ({ carouselId, productIds }) => {
  const [products, setProducts] = useState(null);
  const [loading, setLoading] = useState(true);
  const fetchProducts = async () => {
    if (!carouselId) {
      // หากไม่มี carouselId, ไม่ต้อง fetch และแสดงการโหลด
      setLoading(false);
      return;
    }
    setLoading(true);
    try {
      const response = await getProductCarousel(carouselId, productIds);
      const { data } = response;
      const { code } = data;
      if (code === "no_products" || !data[0]?.product_details?.length) {
        setProducts(null);
        setLoading(false);
        return;
      }
      setProducts(data[0]);
    } catch (error) {
      console.error("Failed to fetch products:", error);
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    setLoading(!!carouselId);
    fetchProducts();
    return () => {};
  }, [carouselId]);

  return <CarouselProduct products={products} loading={loading} />;
};

export default CarouselCon;
