import { create } from "zustand";
import { getProductCarousel } from "@/service";

const useFetchProducts = create((set) => ({
  products: null,
  loading: true,
  fetchProducts: async (carouselId) => {
    set(() => ({ loading: true }));
    try {
      const response = await getProductCarousel(carouselId);
      const { data } = response;
      const { code } = data;
      if (code === "no_products" || !data[0]?.product_details?.length) {
        set(() => ({ products: null, loading: false }));
        return;
      }
      set(() => ({ products: data[0], loading: false }));
    } catch (error) {
      set(() => ({ loading: false }));
    }
  },
}));

export default useFetchProducts;
