import axios from "axios";

const isDevelopment = process.env.NODE_ENV === "development" ? true : false;
const endPointApiDev = process.env.REACT_APP_DEV_ENDPOINT;
const protocol = window.location.protocol; // ดึงโปรโตคอล (http: หรือ https:)
const host = window.location.host; // ดึง host (เช่น localhost:5173)

const apiUrl = isDevelopment ? `${endPointApiDev}` : `${protocol}//${host}`;

// สร้าง instance ของ axios
const axiosInstance = axios.create({
  baseURL: apiUrl,
});

// ตั้งค่า Interceptor สำหรับ Requests
axiosInstance.interceptors.request.use(
  (config) => {
    // // ทำอะไรก็ได้ก่อนที่ request จะถูกส่ง ตัวอย่างเช่น เพิ่ม token ใน headers
    // const token = localStorage.getItem("your-token-storage-key"); // ตัวอย่างการเรียกใช้ token
    // if (token) {
    //   config.headers.Authorization = `Bearer ${token}`;
    // }
    return config;
  },
  (error) => {
    // ทำอะไรก็ได้กับ request error ที่นี่
    return Promise.reject(error);
  }
);

// ตั้งค่า Interceptor สำหรับ Responses
axiosInstance.interceptors.response.use(
  (response) => {
    // ทำอะไรก็ได้กับ response data
    return response;
  },
  (error) => {
    // // ทำอะไรก็ได้กับ response error ที่นี่, ตัวอย่างเช่น จัดการกับ session หมดอายุ
    // if (error.response && error.response.status === 401) {
    //   // ตัวอย่าง: redirect ไปยังหน้า login หรือทำการ refresh token
    // }
    return Promise.reject(error);
  }
);

export default axiosInstance;
