import { useState, useEffect, useLayoutEffect } from "react";
import Carousel from "react-multi-carousel";
import "react-multi-carousel/lib/styles.css";
import { MediaCard, NotFound } from "@/component";
import { isMobile } from "mobile-device-detect";

import { IconButton, Skeleton, Grid } from "@mui/material";
import ArrowBackIosNewIcon from "@mui/icons-material/ArrowBackIosNew";
import ArrowForwardIosIcon from "@mui/icons-material/ArrowForwardIos";
import PropTypes from "prop-types";

const CarouselProduct = ({ products, loading }) => {
  const CustomLeftArrow = ({ onClick }) => {
    if (loading || !products) {
      return <></>;
    }
    return (
      <IconButton
        onClick={onClick}
        style={{
          position: "absolute",
          top: "50%",
          left: "20px",
          transform: "translate(0, -50%)",
          zIndex: 2,
          backgroundColor: "rgba(0,0,0,0.5)",
          color: "white",
          borderRadius: "50%",
          width: "40px",
          height: "40px",
          display: "flex",
          alignItems: "center",
          justifyContent: "center",
        }}
      >
        <ArrowBackIosNewIcon />
      </IconButton>
    );
  };

  const CustomRightArrow = ({ onClick }) => {
    if (loading || !products) {
      return <></>;
    }
    return (
      <IconButton
        onClick={onClick}
        style={{
          position: "absolute",
          top: "50%",
          right: "20px",
          transform: "translate(0, -50%)",
          zIndex: 2,
          backgroundColor: "rgba(0,0,0,0.5)",
          color: "white",
          borderRadius: "50%",
          width: "40px",
          height: "40px",
          display: "flex",
          alignItems: "center",
          justifyContent: "center",
        }}
      >
        <ArrowForwardIosIcon />
      </IconButton>
    );
  };

  return (
    <Carousel
      additionalTransfrom={0}
      arrows
      autoPlaySpeed={4000}
      centerMode={false}
      draggable={isMobile ? true : false}
      customLeftArrow={isMobile ? <></> : <CustomLeftArrow />}
      customRightArrow={isMobile ? <></> : <CustomRightArrow />}
      autoPlay={true}
      className=""
      containerClass="container-with-dots"
      dotListClass=""
      focusOnSelect={true}
      infinite
      itemClass=""
      keyBoardControl
      minimumTouchDrag={80}
      pauseOnHover
      renderArrowsWhenDisabled={false}
      renderButtonGroupOutside={false}
      renderDotsOutside={false}
      responsive={{
        desktop: {
          breakpoint: {
            max: 3000,
            min: 1024,
          },
          items: 4,
          partialVisibilityGutter: 40,
        },
        mobile: {
          breakpoint: {
            max: 464,
            min: 0,
          },
          items: 1,
          partialVisibilityGutter: 30,
        },
        tablet: {
          breakpoint: {
            max: 1024,
            min: 464,
          },
          items: 2,
          partialVisibilityGutter: 30,
        },
      }}
      rewind={false}
      rewindWithAnimation={false}
      rtl={false}
      shouldResetAutoplay
      showDots={false}
      sliderClass=""
      slidesToSlide={1}
      swipeable
    >
      {loading ? (
        Array.from({ length: 4 }).map((_, index) => (
          <MediaCard key={index} loading={true} />
        ))
      ) : products && products?.product_details ? (
        products?.product_details.map((product) => (
          <MediaCard key={product.id} product={product} loading={false} />
        ))
      ) : (
        <NotFound />
      )}
    </Carousel>
  );
};

CarouselProduct.propTypes = {
  products: PropTypes.shape({
    id: PropTypes.string,
    title: PropTypes.string,
    status: PropTypes.string,
    date_created: PropTypes.string,
    date_modified: PropTypes.string,
    language: PropTypes.string,
    product_details: PropTypes.arrayOf(
      PropTypes.shape({
        id: PropTypes.string,
        title: PropTypes.string,
        description: PropTypes.string,
        image: PropTypes.string,
        link: PropTypes.string,
        status: PropTypes.string,
      })
    ),
  }),
  loading: PropTypes.bool,
};

CarouselProduct.defaultProps = {
  products: null,
  loading: false,
};

export default CarouselProduct;
